.description {
  height: 1000px;
  position: relative;
  margin: 0 auto;
  margin-bottom: 2px;
  overflow: hidden;

  &::before {
    position: absolute;
    content: "";
    top: 959px;
    left: 0;
    z-index: 2;
    width: 100%;
    height: 49px;
    margin: 0 auto;
    background-color: #ffffff;
  }

  &::after {
    position: absolute;
    content: "";
    top: 176px;
    left: 0;
    z-index: -1;
    background-image: url("../img/bg-triangle-white.svg");
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    height: 100%;
    transform: rotate(180deg);
  }
}

.description__title {
  font-size: 24px;
  font-weight: 300;
  line-height: 28px;
  color: $midnight;
  text-align: center;
  width: 320px;
  margin: 0 auto;
  margin-bottom: 30px;
  position: relative;

  &::before {
    position: absolute;
    content: "";
    top: 681px;
    left: 82px;
    z-index: 1;
    background: url("..//img/iphone-screen-mobile@1x.png") no-repeat;
    background-size: 157px 320px;
    width: 157px;
    height: 320px;

    @media (min-resolution: 1.5dppx) {
      background-image: url("..//img/iphone-screen-mobile@2x.png");
    }
  }


}

.description__sub-title {
  font-size: 24px;
  font-weight: 300;
  line-height: 28px;
  color: $midnight;
  text-align: center;
  width: 320px;
  margin: 0 auto;
  margin-bottom: 122px;
}

.description__list {
  list-style: none;
  padding: 0;
  margin: 0 auto;
  text-align: center;
}

.description__item-title {
  font-size: 18px;
  color: $midnight;
  line-height: 10px;
}

.description__text {
  font-size: 14px;
  color: $midnight;
  width: 264px;
  margin: 0 auto;
}

.description__item {
  width: 320px;
  height: 120px;
  margin: 0 auto;
}

// TABLE min-width: 660px//////////////////////////////////////////////////////
@media (min-width: 660px) {
  .description {
    height: 665px;
    position: relative;

    &::after {
      position: absolute;
      content: "";
      top: 0;
      left: 0;
      z-index: -1;
      margin-top: 146px;
      background-image: url("../img/bg-triangle-white.svg");
      background-repeat: no-repeat;
      background-size: cover;
      width: 100%;
      height: 100%;
      transform: rotate(180deg);
    }
  }

  .description__list {
    margin-left: 318px;
    text-align: left;
  }

  .description__text {
    margin: 0;
    width: 310px;
  }

  .description__item {
    margin-bottom: 24px;
  }

  .description__title {
    font-size: 40px;
    width: 620px;
    margin-bottom: 9px;
    position: relative;

    &::before {
      position: absolute;
      content: "";
      top: 183px;
      left: 0;
      z-index: 1;
      background: url("..//img/iphone-screen-3-tablet@1x.png") no-repeat;
      width: 268px;
      height: 481px;

      @media (min-resolution: 1.5dppx) {
        background-image: url("..//img/iphone-screen-3-tablet@2x.png");
        background-size: 268px 481px;
      }
    }

    &::after {
      display: none;
    }
  }

  .description__sub-title {
    font-size: 40px;
    line-height: 49px;
    width: 340px;
  }

  .description__table {
    display: flex;
  }

  .description__item-title {
    padding-top: 3px;
  }
} //*min-width: 660px
// DESCTOP min-width: 1200px//////////////////////////////////////////////////////
@media (min-width: 960px) {
  .description {
    height: 850px;
    position: relative;

    &::after {
      position: absolute;
      content: "";
      top: 0;
      left: 0;
      z-index: -1;
      margin-top: 150px;
      background-image: url("../img/bg-triangle-white.svg");
      background-repeat: no-repeat;
      background-size: cover;
      width: 100%;
      height: 100%;
      transform: rotate(180deg);
    }
  }

  .description__title-block {
    height: 180px;
    margin: 0 auto;
    margin-bottom: 382px;
  }

  .description__title {
    font-size: 50px;
    position: relative;
    width: 100%;
    margin: 0 auto;
    margin-bottom: 16px;

    &::before {
      display: none;
    }
  }

  .description__sub-title {
    font-size: 50px;
    width: 430px;
    height: 140px;
    margin: 0 auto;
    line-height: 59px;
  }

  .description__item-title {
    font-size: 30px;
    line-height: 30px;
    text-align: center;
    width: 230px;
    margin-bottom: 25px;
  }

  .description__item-phone-1 {
    position: relative;

    &::before {
      position: absolute;
      content: "";
      top: -325px;
      left: -30px;
      z-index: 1;
      background: url("..//img/iphone-screen-1-desktop@1x.png") no-repeat;
      width: 253px;
      height: 304px;

      @media (min-resolution: 1.5dppx) {
        background-image: url("..//img/iphone-screen-1-desktop@2x.png");
        background-size: 292px 302px;
      }
    }
  }

  .description__item-phone-2 {
    position: relative;

    &::before {
      position: absolute;
      content: "";
      top: -325px;
      left: -27px;
      z-index: 1;
      background: url("..//img/iphone-screen-2-desktop@1x.png") no-repeat;
      width: 253px;
      height: 304px;

      @media (min-resolution: 1.5dppx) {
        background-image: url("..//img/iphone-screen-2-desktop@2x.png");
        background-size: 292px 302px;
      }
    }
  }

  .description__item-phone-3 {
    position: relative;

    &::before {
      position: absolute;
      content: "";
      top: -325px;
      left: -27px;
      z-index: 1;
      background: url("..//img/iphone-screen-3-desktop@1x.png") no-repeat;
      width: 253px;
      height: 304px;

      @media (min-resolution: 1.5dppx) {
        background-image: url("..//img/iphone-screen-3-desktop@2x.png");
        background-size: 292px 302px;
      }
    }
  }

  .description__list {
    display: flex;
    justify-content: space-between;
    margin: 0 auto;
    margin-left: 44px;
  }

  .description__text {
    font-size: 18px;
    line-height: 30px;
    text-align: center;
    color: $midnight;
    width: 240px;
    padding-top: 1px;
    margin-left: -4px;
  }
} //*min-width: 1200px
