

.btn {
  width: 280px;
  font-size: 18px;
  margin: 0 auto;
  padding: 15px;
  cursor: pointer;
  border: 2px solid #cecece;
  background: #f6f6f6;
  border-radius: 30px;
}

.btn__post-submit {
  margin-bottom: 13px;
  border-color: #d22856;
  color: #d22856;
  background-color: inherit;
}

.btn__post-reset {
  background-color: inherit;
  border-color: #dddfe0;
  color: rgba(40, 54, 69, 0.1);
}

// TABLET min-width:660px /////////////////////////////////////////
@media (min-width: 660px) {
  .filters__btn-post {
    margin: 0;
    height: 140px;
  }

  .btn {
    width: 257px;
  }

  .btn__post-submit {
    margin-left: 19px;
    margin-bottom: 27px;
  }

  .btn__post-reset {
    margin-left: 19px;
  }
} //min-width: 660px
// DESCTOP min-width:1200px /////////////////////////////////////////
@media (min-width: 960px) {
  .btn {
    width: 185px;
  }

  .filters__btn-post {
    height: 60px;
    width: 50%;
  }

  .btn__post-submit {
    margin-left: -66px;
  }
} //min-width: 1200px
